body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: #e0c880;
}

.App {
  text-align: center;
}

.page-instructions {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 4px;
}

.page-footer {
  margin-top: 20px;
  margin-bottom: 1px;
  border: 1px solid black;
  padding: 4px;
  background-color: white;
}

.social-card {
  display: flex;
}

.social-card-piece {
  min-height: 50px;
  display: flex;
  margin: 2px;
  border: 1px solid black;
  background-color: white;
  align-items: center;
}

.social-card-name {
  display: flex;
  align-items: center;
}

.social-card-npc {
  flex: 1 1;
  min-width: 190px;
  padding-left: 5px;
}

.social-card-gifts {
  flex: 14 1;
}

.social-card-optimal-gift {
  max-width: 60px;
  flex: 1 1;
}

.social-card-gift-list {
  display: flex;
  flex-wrap: wrap;
}

.social-card-single-gift {
  border-radius: 6px;
  border: 1px solid black;
  margin: 2px;
  font-size: 12px;
  padding: 1px 4px 1px 4px;
}

.social-card-avatar {
  height: 30px;
}

.social-card-birthday-icon {
  height: 28px;
}

.social-card-info {
  margin-left: 5px;
  padding-bottom: 3px;
  text-align: left;
}

.social-card-info p {
  font-size: 15px;
  margin-top: 0px;
  margin-bottom: 2px;
}

.social-card-points {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2px;
}

.social-card-additional-text {
  font-size: 12px;
}

.social-card-heart {
  width: 13px;
  padding: 1px;
}

.birthday-card {
  display: flex;
  background-color: white;
  border: solid 1px black;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 50px;
  padding: 8px;
  margin-top: 2px;
  margin-bottom: 2px;
}

.winter-star-card {
  display: flex;
  background-color: white;
  border: solid 1px black;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 50px;
  padding: 8px;
  margin-top: 2px;
  margin-bottom: 2px;
}

.current-day-card {
  display: flex;
  background-color: white;
  border: solid 1px black;
  align-items: center;
  text-align: center;
  justify-content: center;
  height: 30px;
  width: 120px;
  padding: 8px 4px 8px 4px;
  margin: 4px;
}

.current-day-card img {
  height: 35px;
  padding-right: 5px;
}

